import mongoose, { Schema, Document, Model } from 'mongoose';

// Model definition for 3D models in the store
export interface IModel {
  url: string;
  position: {
    x: number;
    y: number;
    z: number;
  };
  size: number;
  id: string;
}

// Store document interface
export interface IStore extends Document {
  name: string;
  imageUrl: string;
  models: IModel[];
  videoUrl?: string;
  clickableLink?: string;
  domain?: string;
  createdAt: Date;
  updatedAt: Date;
}

const ModelSchema = new Schema({
  url: { type: String, required: true },
  position: {
    x: { type: Number, required: true, default: 0 },
    y: { type: Number, required: true, default: 0 },
    z: { type: Number, required: true, default: 0 }
  },
  size: { type: Number, required: true, default: 1 },
  id: { type: String, required: true }
});

const StoreSchema = new Schema<IStore>(
  {
    name: { type: String, required: true, unique: true },
    imageUrl: { type: String, required: true },
    models: [ModelSchema],
    videoUrl: { type: String },
    clickableLink: { type: String },
    domain: { type: String }
  },
  {
    timestamps: true
  }
);

// Prevent model recompilation during hot reload in development
const Store: Model<IStore> = mongoose.models.Store || mongoose.model<IStore>('Store', StoreSchema);

export default Store;
